.gpt3__header{
    display: flex;
}

.gpt3__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.gpt3__header-content h1{
    font-family: var(--font-family) ;
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
    text-align: left;

}
.gpt3__header-content p{
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: 400;
    line-height: 27.32px;
    text-align: left;
    color: var(--color-text);

    margin-top: 1.5rem;
}

.gpt3__header-content__input{
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.gpt3__header-content__input input{
    flex: 2;
    width: 100%;
    min-height: 50px;
    background: var(--color-footer);
    border-radius: 5px 0px 0px 5px;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #bbb4b4;
}

.gpt3__header-content__input button{
    flex: 0.6;
    width: 100%;
    min-height: 50px;

    font-family: var(--font-family);
    font-size: 20px;
    font-weight: 700;
    line-height: 27px;
    text-align: center;
    background: #FF4820;
    border-radius: 5px 0px 0px 5px;
    color: #fff;
    outline: none;
    padding: 0 1rem;
    cursor: pointer;
}

.gpt3__header-content__people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-top: 2rem;
}

.gpt3__header-content__people img{
      width: 181.79px;
      height: 38px;
}

.gpt3__header-content__people p{
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 37.61px;
    text-align: center;
    color : #FFFFFF;

}

.gpt3__header-image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-image img{
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .gpt3__header{
        flex-direction: column;
    }

    .gpt3__header-content{
        margin: 0 0 2rem ;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__header h1{
        font-size:48px;
        line-height: 60px;
    }

    .gpt3__header p{
        font-size:16px;
        line-height: 24px;
    }


    .gpt3__header-content__people{
       flex-direction: column;
    }

    .gpt3__header-content__people p{
        margin: 0;
    }

    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .gpt3__header h1{
        font-size: 36px;
        line-height: 40px;
    }

    .gpt3__header p {
        font-size: 14px;
        line-height: 24px;
    }

    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 12px;
        line-height: 16 px;
    }
}